<template>
    <!-- appointments -->
    <div class="appointments">
        <div class="accordion" id="accordionExample3">
            <div class="card">
            <div class="card-header" id="headingOne3">
                <h2 class="mb-0">
                <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne3" aria-expanded="true" aria-controls="collapseOne3">
                    <div class="detais-title">
                    <h3><img class="img-fluid arrow-down" src="../../assets/img/arrow-down.svg" alt="">Appointments <a class="add-appoi add-appi" href="#"  v-on:click.stop.prevent="toggleAddAppointment('show')"><img class="img-fluid" src="../../assets/img/24. Close.svg" alt=""> add</a></h3>
                    </div>
                    <p>{{todayDate}} <img class="img-fluid" src="../../assets/img/Vector (4).svg" alt=""></p>
                </button>
                </h2>
            </div>

            <div id="collapseOne3" class="collapse show" aria-labelledby="headingOne3" data-parent="#accordionExample3">
                <div class="card-body">
                    <simplebar class="active-meeting" id="myElement2">
                        <!-- active-meeting -->
                        <div class="media" v-for="apt in lAppointments" :key="apt.ap_id">
                            <p class="mr-3">{{apt.ap_created | formatDate('HH:mm')}}</p>
                            <div class="media-body" :class="aptColors[Math.floor(Math.random()*(3-1+1)+1)]">
                                <h5 class="mt-0">{{apt.ap_description}}</h5>
                                <p>{{apt.ap_date_time | formatDate('hh:mm A')}}</p>
                            </div>
                        </div>
                        <div class="media not-found" v-if="lAppointments.length == 0">No Appointmrnt Found.</div>
                        <!-- active-meeting simplebar -->
                    </simplebar>
                </div>
            </div>
            </div>
        </div>
        <!-- add-appointments -->
        <div class="add-appointments">
            <div class="modal-header">
                <h5 class="modal-title">Add Appointment</h5>
                <a class="close hide-appi" data-dismiss="modal" aria-label="Close"  v-on:click="toggleAddAppointment('hide')">
                <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                </a>
            </div>
            <form v-on:submit.prevent = "addNewAppointment">
            <div class="add-body">
                
                <div class="form-group">
                    <label for="inputAddress">Date</label>
                    <!-- :min-date="new Date().setDate(new Date().getDate() - 1)" -->
                    <date-range-picker :locale-data="{ format: 'mmmm dd, yyyy' }"  v-model="addApt.ufDate" :single-date-picker="true" :show-dropdowns="true" :auto-apply="true" :ranges="false"></date-range-picker>
                    <!-- <input type="date" class="form-control" placeholder="Pick your date" v-model="addApt.date"> -->
                </div>
                <div class="form-row">
                    <div class="form-group col-md-4">
                    <label for="inputCity">Time</label>
                    <select class="form-control" v-model="addApt.tHour">
                        <option value="">hours</option>
                        <option v-for="(n, index) in 12" :key="index" v-bind:value="n">{{n}}</option>
                    </select>
                    </div>
                    <div class="form-group col-md-4">
                    <label for="inputState"></label>
                    <select class="form-control" v-model="addApt.tMin">
                        <option value="">minutes</option>
                        <option v-for="(n, index) in 60" :key="index" v-bind:value="n-1 | numberStep('2')">{{n-1 | numberStep('2')}}</option>
                    </select>
                    </div>
                    <div class="form-group col-md-4">
                    <label for="inputZip"></label>
                    <select class="form-control" v-model="addApt.tDtype">
                        <option value="am">am</option>
                        <option value="pm">pm</option>
                    </select>
                    </div>
                </div>
                <div class="form-group" v-if="$storage.get('auth').user.user_type == 1">
                    <label for="inputAddress2">Assign to</label>
                    <select class="form-control" v-model="addApt.assignTo">
                    <option v-for="emp in employeesList" :key="emp.id" :value="emp.id">{{emp.name}}</option>
                    <!-- <option>Brandon Wilson</option>
                    <option>Brandon Wilson</option> -->
                    </select>
                </div>
                <div class="form-group">
                    <label for="inputAddress2">Description <span>(optional)</span></label>
                    <textarea class="form-control" v-model="addApt.desc" id="" rows="5" placeholder="Text drive and trade"></textarea>
                </div>

                
            </div>

            <div class="modal-footer app-footer">
                <a href="#" class="add-btn" v-on:click="toggleAddAppointment('hide')"> Back</a>
                <button type="submit" class="rem-btn"><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Add</button>
            </div>
            </form>
        </div>
        <!-- add-appointments -->
    </div>
    <!-- appointments -->
</template>
<script>
import axios from 'axios'
import VueDaval from 'vue-daval'
import $ from 'jquery'
import moment from 'moment'
import simplebar from 'simplebar-vue';
import 'simplebar/dist/simplebar.min.css';

import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'
export default {
    name: 'Appointments',
    props: ['defaultAssignTo','employeesList'],
    components: {
        simplebar,DateRangePicker,
    },
    mixins: [ VueDaval ],
    data(){
		return{
            lAppointments: [],
            todayDate: moment().format('MMMM DD, dddd'),
            addApt : {date:'',ufDate:{},tHour:'',tMin:'',tDtype:'am',assignTo:this.$props.defaultAssignTo,desc:''},
            aptColors: ['','green-meeting','blue-meeting','red-meeting']
        }
    },
    vdRules: {
        addApt:  {
            ufDate:{required: true},tHour:{required: true},
            tMin:{required: true}, tDtype: {required: true},
            desc:{required: true}
        }
    },
    created(){
        if(this.$storage.get('auth').user.user_type == 2){
            this.getAppointments(0)
        }else if(this.$storage.get('auth').user.user_type == 1){
            this.getAppointments(this.$route.params.id)
        }
    },
    methods:{
		getAppointments(id){
			axios
			.get('appointments/'+id,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.lAppointments = data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        toggleAddAppointment(action){
            if(action == 'show'){
                $(".add-appointments").addClass("show-appointments");
            }else if(action == 'hide'){
                $(".add-appointments").removeClass("show-appointments");
            }
            
        },
        addNewAppointment(){
            if(this.$storage.get('auth').user.user_type == 1){
                this.addApt.lead_id = this.$route.params.id
            }
            let startDate = this.addApt.ufDate.startDate;
            this.addApt.date = moment(new Date(startDate)).format('YYYY-MM-DD');

            this.$vd.addApt.$validate().then(() => {
                //console.log(this.addApt);
                axios
                .post('add_appointment',this.addApt,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        this.toggleAddAppointment('hide')
                        this.addApt = {date:'',ufDate:{},tHour:'',tMin:'',tDtype:'am',assignTo:this.$props.defaultAssignTo,desc:''}
                        if(this.$storage.get('auth').user.user_type == 1){
                            this.getAppointments(this.$route.params.id)
                            this.$root.$emit('newActivity', 'added')
                        }else{
                            this.getAppointments(0)
                        }
                    }
                })
                .catch(err =>{
                    console.log(err)
                });

            }).catch(err => {
                console.log(err)
            });
        },
    }
}
</script>