<template>
    <!-- note-area -->
    <div class="note-area">
        <div class="accordion" id="accordionExample5">
            <div class="card">
            <div class="card-header" id="headingOne5">
                <div class="mb-0">
                <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne5" aria-expanded="true" aria-controls="collapseOne5">
                    <div class="detais-title">
                    <h3><img class="img-fluid arrow-down" src="../../assets/img/arrow-down.svg" alt="">  Notes </h3>
                    </div>
                </button>
                </div>
            </div>

            <div id="collapseOne5" class="collapse show" aria-labelledby="headingOne5" data-parent="#accordionExample5">
                <div class="card-body">
                    <div class="form-group">
                        <input type="text" v-on:keyup.enter="addNote" v-model="note" class="form-control" placeholder="Start typing to add a note...">
                    </div>
                    <simplebar class="all-notes">
                        <!-- active-Tasks -->
                        <div class="media">
                            <div class="media-body">
                                <p v-for="nt in notes" :key="nt.no_id">{{nt.no_details}}<br><span>{{nt.created}}</span></p>
                                <p class="not-found" v-if="notes.length == 0">No Note Found.</p>
                            </div>
                        </div>
                    </simplebar>
                </div>
            </div>
            </div>
        </div>
    
    </div>
    <!-- note-area -->
</template>
<script>
import axios from 'axios'
import simplebar from 'simplebar-vue';
import 'simplebar/dist/simplebar.min.css';
export default {
    name: 'Notes',
    data(){
		return{
            note : '',
            notes:[]
        }
    },
    components: {
        simplebar
    },
    created(){
        if(this.$storage.get('auth').user.user_type == 2){
            this.getNotes(0)
        }else if(this.$storage.get('auth').user.user_type == 1){
            this.getNotes(this.$route.params.id)
        }
    },
    methods:{
        addNote(){
            if(this.note != '' && this.note != undefined && this.note != 'undefined' && this.note != null ){
                var param;
                if(this.$storage.get('auth').user.user_type == 2){
                    param = {note:this.note}
                }else if(this.$storage.get('auth').user.user_type == 1){
                    param = {note:this.note,lead_id:this.$route.params.id}
                }
                axios
                .post('add_note',param,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        this.notes.unshift(res.data.success)
                        this.note = ''
                        if(this.$storage.get('auth').user.user_type == 1){
                            this.$root.$emit('newActivity', 'added')
                        }
                    }
                })
                .catch(err =>{
                    console.log(err)
                });
            }
        },
        getNotes(id){
            axios
            .get('notes/'+id,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
            .then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.notes = data.success
                }
            })
            .catch(err =>{
                console.log(err)
            })
        },
    }
}
</script>